import { Paper } from '@mui/material';
import React from 'react';
import { useAuth } from '../../../hooks/useAuth';
import useMockData from '../../../hooks/useMockData';
import Container from '../../common/Container';
import { SearchRoomsForm } from '../../ui/forms';

const HomePage = () => {
  const { currentUser } = useAuth();
  const { error, initialize, progress, status } = useMockData();

  const handleClick = () => {
    console.log('clicked');
    initialize();
  };

  return (
    <main className='main-home__page'>
      <Container>
        <div className='main-home__wrapper'>
          <h1 className='visually-hidden'>    toxin result school</h1>
          <Paper elevation={3} className='form-card searchRooms-form'>
            <h2>    </h2>
            <SearchRoomsForm />
          </Paper>
          <p className='main__text-wishes'>    ,    </p>
        </div>
        {currentUser?.role === 'admin' && (
          <>
            <h3>   FireBase</h3>
            <ul>
              <li>Status: {status}</li>
              <li>Progress: {progress}%</li>
              {error && <li>error: {error}</li>}
            </ul>
            <button className='btn btn-primary' onClick={handleClick}>
              
            </button>
          </>
        )}
      </Container>
    </main>
  );
};

export default HomePage;
