import React from 'react';
import { SelectField } from '../../../common/Fields';

const roomsSortArray = [
  { name: ' ', value: { path: 'numberRoom', order: 'desc' } },
  { name: ' ', value: { path: 'numberRoom', order: 'asc' } },
  { name: '', value: { path: 'countReviews', order: 'desc' } },
  { name: ' ', value: { path: 'rate', order: 'desc' } },
  { name: ' ', value: { path: 'rentPerDay', order: 'asc' } },
  { name: ' ', value: { path: 'rentPerDay', order: 'desc' } },
];

const RoomsSort = ({ sortBy, onSort }) => {
  return (
    <SelectField
      style={{ minWidth: '200px' }}
      label=''
      value={JSON.stringify(sortBy)}
      onChange={onSort}
      options={roomsSortArray}
    />
  );
};

export default RoomsSort;
